import React, { Component } from "react";
import { TopicForm } from "./TopicForm";
import { RouteComponentProps } from "react-router-dom";
import { Translation, useTranslation } from "react-i18next";
import { Heading, Button, Box } from 'grommet';
import MenuCompnent from "../../menu/Menu";
import { menuConfig } from "../../../../shared/constants/menuItems";
import { Add } from 'grommet-icons';

class TopicPage extends Component<RouteComponentProps>  {
  state = {};

  render(): JSX.Element {
    document.body.className = "";
    return (
      <Translation>
        {(t) => (
          <div>
            <MenuCompnent items={menuConfig} />
            <Heading level="2" fill textAlign="center">{t('Blog')}</Heading>
            <Box round="full" overflow="hidden" background="#ADFF00">
              <Button icon={<Add />} hoverIndicator onClick={() => { }} />
            </Box>
            <TopicForm {...this.props} />
          </div>
        )}
      </Translation>
    );
  }
}

export default TopicPage;